import Vue from 'vue'
import Router from 'vue-router'
// import HomeContain from '@/components/tabbar/HomeContain.vue'
import MiHome from '@/page/home/MiHome.vue';
import Store from '@/page/home/Store.vue';
import ImageViewer from '@/components/subcomponents/imageviewer.vue';
import Profile from '@/page/home/Profile.vue';
import Automation from '@/page/home/Automation.vue';

Vue.use(Router)

export default new Router({
  // mode:'history',
  mode:'hash',
  routes: [
    {
      path: '/',
      redirect:"/mihome"//重定向 如果访问/  自动将请求路径改为  /home
    },
    {
      path: '/mihome',
      name: 'mihome',
      component: MiHome,
      children:[
        {path:'',name:'imageviewer',component:ImageViewer}
      ]
    },
    {
      path:'/store',
      name:'store',
      component:Store
    },
    {
      path:'/profile',
      name:'profile',
      component:Profile
    },
    {
      path:'/automation',
      name:'automation',
      component:Automation
    }
  ]
})



