  <script>
  export default {
    data(){
      return{
        timers:true
      }
    },
    mounted(){
      this.myfunction();
    },
    methods:{
      myfunction(){
        let _self = this;
        // 设置定时器--时间戳--当前时间
        let now = new Date();
        let Year = now.getFullYear();
        // console.log(Year);
        let Month =now.getMonth()+1;
        // console.log(now.getMonth()+1);
        Month<10&&(Month="0"+Month);
        let day =now.getDate();
        day<10&&(day="0"+day);
        // 初始时间
        let end = new Date(`${Year}-${Month}-${day} 14:00:00`);
        // console.log(start);
        // var d=now.getDate();
        // var s=start.getDate();
        // console.log(d);
        // console.log(s);
        // var day = d-s;
        // if (day > 0) {
        //   var today = start.getTime() + (24*60*60*1000)*day;
        // } else {
        //   var today = start.getTime() + (24*60*60*1000);
        // }
        function calc(){
          // let end=new Date(today);
          let start=new Date();
          let t=parseInt((end-start)/1000);
          // console.log(today);
          if(t>0){
              let d=parseInt(t/(3600*24));
              let h=parseInt(t%(3600*24)/3600);
              h>12&&(h-=12);
              h<10&&(h="0"+h);
              let m=parseInt(t%3600/60);
              m<10&&(m="0"+m);
              let s=t%60;
              s<10&&(s="0"+s);
              // 解决js先与html内容元素先加载的问题
              if(_self.$refs.time3){
                _self.$refs.time1.innerHTML=`${h}:`;
                _self.$refs.time2.innerHTML=`${m}:`;
                _self.$refs.time3.innerHTML=`${s}`;
              }
          }else{
            clearInterval(timer);
            timer=null;
            // _self.$refs.time.innerHTML=`活动已结束`;
            _self.timers=false;
          }
        };
        calc();
        // 下面逻辑是解决定时器重复叠加的问题
        if(timer){
          clearInterval(timer);
          timer=null;          
        }else{
          var timer=setInterval(calc,1000);//这里不能用let声明，因为let是块级作用域
          this.timers=true;
        }
        // console.log(`我是第一个定时器${timer}`);
        }
      }
    }
  
  </script>
  
