      margin:0;
      padding:1px;
      width:60px;
      border:2px solid white;
    }

    div.store>footer>button>span{
      font-size:15px;
      margin:0;
      padding:0px;
    }

    div.store>footer>div{
      padding:25px;
    }

    /* 底线 */
    .store>h6{
      margin:10px 0 60px;
      text-align: center;
      padding:0;
    }

    .store>h6>div{
      display: inline-block;
      width:50px;
      border:0.5px solid rgb(102, 102, 102,.5);
    }  

  .aa{
    width:100px;
    height:100px;
  }
  </style>
  


