import Vue from 'vue'
import App from './App.vue'
//1:加载路由模块
import router from './router'
//1.1加载模块 vueResource(发送ajax请求) //vue-resource是库名，大小写不能变
import VueResource from 'vue-resource';
//1.2:将VueResource  注册vue  //这里用use是因为要引用VueResource所有组件
Vue.use(VueResource);
// // 设置muijs
// import mui from './lib/mui/js/mui.js'
// import "@/assets/css/base.css"
// Vue.prototype.mui = mui
//1.2.1:设置请求根路径（服务器地址）(这样以后就不用写开头的路径，因为根路径可能会变)
Vue.http.options.root="http://127.0.0.1:3000/";
//1.2.2:创建全局过滤器
Vue.filter("datetimeFilter",function(val){
  var date=new Date(val);
  var y=date.getFullYear();
  var m=date.getMonth()+1;
  m<10&&(m="0"+m);
  var d=date.getDate();
  d<10&&(d="0"+d);
  var h=date.getHours();
  h>12&&(h-=12);
  h<10&&(h="0"+h);
  var M=date.getMinutes()
  M<10&&(M="0"+M);
  return `${y}-${m}-${d} ${h}:${M}`;
});
// 音乐的标题的过滤器
Vue.filter("ellipses",function(val){//val是原始值，len是它想保留的字的个数
  return val.split('/')[4].split('.')[0];
});

//解决mui-bar-tab无法跳转的问题
// mui('body').on('tap','a',function(){document.location.href=this.href;});
// mui('.mui-bar-tab').on('tap','a',function(){document.location.href=this.href;});
// mui('.mui-bar-tab').on('tap','a',function(){  
//   // console.log(this.getAttribute('href'));  
//   location.href = this.getAttribute('href');  
// })

//完整的引入MintUI所有组件
import MintUI from 'mint-ui'
Vue.use(MintUI)

//1.6:引入mui css
import "./lib/mui/css/mui.css";
import "./lib/mui/css/icons-extra.css"  //购物车

//1.7:引入mint-ui 组件所需样式文件
import "mint-ui/lib/style.css"  //高版本的不需要引入css，引入组件就可以了

//1.8:引入echarts
import echarts from 'echarts'
Vue.prototype.$echarts = echarts 

import "@/assets/css/base.css"

//2：是否是生成模式 false
Vue.config.productionTip = false

//2.1:将vuex添加到项目中
//第一步：导入vuex
import Vuex from "vuex";
//第二步：将vues注册vue
Vue.use(Vuex);
//第三步：创建vuex实例[共享数据;提供操作数据方法;获取数据方法]
var store = new Vuex.Store({

});

/* eslint-disable no-new */
new Vue({//Vue实例
  router,//路由
  render: h => h(App),//渲染
  store//上面创建的实例对象挂载到Vue实例里
}).$mount('#app')



