<template>
  <div class="main">
    <div id="myChart" :style="{width: '100%', height: '300px'}"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      
    }
  },
  mounted(){
    this.drawLine();
  },
  methods: {
    drawLine(){
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById('myChart'))
      // 绘制图表
      myChart.setOption({
        title:{text:"智能家居使用频率（日均）"},
        tooltip: {},
        xAxis: {
          data: ["电视","电灯","电饭锅","吸尘器","电子门","电脑"]
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'line',
          data: [100,250,80,15,50,300]
        }]
      });
    }
  }
}
</script>

<style scoped>
  *{
    font-family:"Microsoft Yahei","Microsoft JhengHei","SimHei";
  }
  
  .main{
    margin:10px 3px;
    width:100%;
  }
  
</style>



