<template>
  <div class="main">
    <div class="app-container" v-show="n>=1">
      <span>{{n}}秒钟后自动关闭</span>
    </div>
    <!-- 1:顶部固定导航栏
    <mt-header>
      <h2 slot="left">聚众力</h2>
    </mt-header> -->
    <!--2:多个组件切换  router-view-->
    <router-view v-show="n<=0"></router-view>
    <!--3:tabbar  固定在底端的导航条-->
    <nav class="mui-bar mui-bar-tab" v-show="n<=0">
      <router-link class="mui-tab-item mui-active" to="/">
        <span class="mui-icon mui-icon-home-filled"></span>
        <span class="mui-tab-label">Mi Home</span>
      </router-link>
      <router-link class="mui-tab-item" to="/store">
        <span class="mui-icon mui-icon-extra mui-icon-extra-gift"></span>
        <span class="mui-tab-label">Store</span>
      </router-link>
      <router-link class="mui-tab-item" to="/automation">
        <span class="mui-icon mui-icon-location-filled"></span>
        <span class="mui-tab-label">Automation</span>
      </router-link>
      <router-link class="mui-tab-item" to="/profile">
        <span class="mui-icon mui-icon-contact"></span>
        <span class="mui-tab-label">Profile</span>
      </router-link>        
    </nav>
  </div>
</template>

<script>
export default {
  data(){
    return{
      n:5,//正式生产再改回5秒
      timer:''
    }
  },
  created(){
    // window.addEventListener('touchmove', func, { passive: false }) //注册处理函数时，用如下方式，明确声明为不是被动的
  },
  mounted(){
    this.times();
  },
  methods:{
    show(){
      if(this.n>=1){
        this.n--
      }else{
        // console.log(12);
        clearInterval(this.timer);
        this.timer=null;
      }
    },
    times(){
       this.timer = setInterval(this.show,1000);
    }
  }
}
</script>


<style>
/*mt-header  固定顶部布局显示组件*/
/*tabbar  固定底部布局显示组件*/
/* .app-container{
   padding-top:40px; 
   padding-bottom:50px; 
} */
/* 做个背景图 */
html,body{
  height:100%; 
  width:100%; 
  /* overflow:hidden;  */
  margin:0;
  padding:0;
  /* text-align: center; */
}

.app-container{
  height:100%;
  width:100%;
  background-image:url('/../static/img/ad/background.jpg');
  background-repeat: no-repeat;
  background-size:cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}

.main{
  height:100%;
  width:100%;
  /* padding-top:40px;
  padding-bottom:50px; */
}

.mui-bar{
  background-color:rgba(0, 0, 0, .5);
}

a{
  color:#FFFFFF;
  touch-action: none;
}
</style>



