<template>
  <div class="MihomeMain">
    <div class="page-navbar" id="left" ref="left">
      <div class="page-title">
        <h4 class="mui-icon mui-icon-home">1121379056's</h4>
        <span class="mui-icon mui-icon-plusempty add" ref="spanAdd" @click="rightSide"></span>
      </div>
      <mt-navbar class="page-part" v-model="selected">
        <mt-tab-item id="1">My devices</mt-tab-item>
        <mt-tab-item id="2">Household</mt-tab-item>
        <mt-tab-item id="3">Default</mt-tab-item>
        <span class="mui-icon mui-icon-arrowdown" @click="isLoginBtn"></span>
      </mt-navbar>
      <!-- 模态框的背景层 -->
      <section class="modalBox" v-show="isLogin" @click.stop="isLoginBtn2">
      </section>
      <!-- 模态框 -->
      <div class="content" v-show="isLogin">
        <ul>
          <li>My devices</li>
          <li>Default room</li>
          <li>Router</li>
          <li>Management</li>
        </ul>
      </div>

      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <!-- <mt-cell v-for="n in 10" :title="'内容 ' + n" :key="n.id"/> -->
          <div class="my-box" @click="show()">
            <h5>Tenda_4C1538</h5>
            <h6>Device online</h6>
            <!-- 用base64代码表示路由图标 -->
            <img src="data:image/png;base64," alt="">
          </div>
        </mt-tab-container-item>
        <mt-tab-container-item id="2" class="tabItem2">
          <!-- 音乐部分 -->
          <h3>{{this.bodyBgs[this.random].mp3|ellipses}}</h3>
          <img :src="bodyBgs[this.random].img">
          <audio :src="bodyBgs[this.random].mp3" controls autoplay ref="box">
            您的浏览器版本太低请升级！！
          </audio> 
          <!-- 做自定义播放按钮组 -->
          <label>
              <!-- <input type="checkbox" @change="btnPlay"> -->
              <div @click="btnPlay">播放</div>
              <div @click="prev">上一首</div>
              <div @click="next">下一首</div>
          </label>          
        </mt-tab-container-item>
        <mt-tab-container-item id="3" class="tabItem3">
        <!-- 视频部分 -->
          <video :src="this.video" controls loop preload="metadata" id="v2">
            您的浏览器版本太低，请升级！
        </video>
        </mt-tab-container-item>
        <mt-tab-container-item id="4">

        </mt-tab-container-item>        
      </mt-tab-container>
    </div>
    <!-- 右边的侧边栏部分 -->
    <aside id="right" ref="right">
      <router-view></router-view>
    </aside>
  </div>
</template>

<script>
  // 创建一个过滤器
export default {
  data() {
    return {
      selected: '1',
      swipeBack:true,
      isLogin:false,
      bodyBgs:[
        {mp3:'./static/img/mp3/倩儿 - 等你下课.mp3',img:'./static/img/mp3/dnxk.jpg'},
        {mp3:'./static/img/mp3/往后余生.mp3',img:'./static/img/mp3/whys.jpg'},
        {mp3:'./static/img/mp3/演员.mp3',img:'./static/img/mp3/yy.jpeg'}
      ],
      random:0,
      checkBtn:0,
      video:'./static/img/mp4/intelligence.mp4',
      expansion:false //写开关门的值
    };
  },
  created(){
    this.randomBgIndex();
  },
  methods:{
    isLoginBtn(){
      if(!this.isLogin){
        this.isLogin=true;
      }else{
        this.isLogin=false;
      }
    },
    isLoginBtn2(){
      this.isLogin=false;
    },
    randomBgIndex(){
      this.random = Math.floor( Math.random() * 3);
      // console.log(this.random);
      // this.$refs.box.style.background="#F0F0F2";
      // console.log(this.$refs.box);
    },
    btnPlay(){
      // console.log(this.$refs.box); 
      if(this.checkBtn == 0){
        //2.1:当前音频音量 0
        // this.$refs.box.volume=0;
        // //2.2:播放音乐
        this.$refs.box.play();
        // //2.3:创建定时器
        // var t=setInterval(function(){
        //   //2.4:修改音量
        //   this.$refs.box.volume += 0.1;
        //   //2.5:当音量大于0.9
        //   if(this.$refs.box.volume>0.9){
        //     //2.6:停止定时器
        //     clearInterval(t)
        //   }
        // },200);
        this.checkBtn = 1;
      }else{
      //3:停止播放
        //3.1:获取当前音量
        // console.log(this.$refs.box);
        var v=this.$refs.box.volume;
        this.checkBtn = 0;
        this.$refs.box.pause();
        //3.2:创建定时器
        // var t=setInterval(function(){
        //   //3.3:修改音量 -0.1
        //   v -= 0.1;
        //   //3.4:如果小于0 停止定时器 暂停播放
        //   // console.log(this.$refs.box);
        //   if(v>=0){
        //     // console.log(123);
        //     // console.log(this.$refs.box);
        //     this.$refs.box.volume=v;
        //   }else{
        //     clearInterval(t);
        //     this.$refs.box.pause();
        //   }
        // },200);
      }
    },
    prev(){
      if(this.random >= 1){
        this.random--;
        // console.log(this.random);
      }else{
        // console.log("已经是第一首");
      }
    },
    next(){
      if(this.random < 2){
        this.random++;
        // console.log(this.random);
      }else{
        // console.log("已经是最后一首");
      }
    },
    rightSide(){//写开关门的逻辑
			if(this.expansion == false){
				//display="none"; //传统的css样式display不支持过渡,不是所有属性都支持过渡,因此用width代替
        this.$refs.left.style.width="0";
        this.$refs.right.style.width="100%";
        this.$refs.spanAdd.style.color="black";
        this.$refs.spanAdd.className="mui-icon mui-icon-arrowleft arrow";
        this.$refs.spanAdd.style.border="1px solid #666666";
        // this.$refs.spanAdd.style.right="50px;"
				this.expansion=true;
			}else{
        this.$refs.left.style.width="100%";
				this.$refs.right.style.width="0";
        this.$refs.spanAdd.style.color="white";
        this.$refs.spanAdd.className="mui-icon mui-icon-plusempty add";
        this.$refs.spanAdd.style.border="";
				this.expansion=false;
			}      
    },
    show(){
      this.$toast({
        message:"ununited",//提示框显示消息
        position:"middle",//提示框位置
        duration:2000,//提示框显示时长（ms）  //调成-1就一直显示
        className:"mytoast",//自定义样式
        iconClass:"mytip"//自定义图标
      });
    }
  }
};
</script>

<style scoped>
.MihomeMain{
  height:100%;
  width:100%;
  background-image:url('../../../static/img/home/01.jpg'); 
  background-repeat: no-repeat;
  background-size:cover;  
  position: relative;
}

.MihomeMain>.page-navbar>.page-title{
  display: flex;
  justify-content: space-between;
  padding:5px;
}

.MihomeMain>.page-navbar>.page-title>h4{
  color:#FFFFFF;
  font-size:20px;
}

.MihomeMain>.page-navbar>.page-title>span{
  color:#FFFFFF;
  position:fixed;
  /* right:10px; */
  top:10px;
  z-index: 1000;
  border-radius: 50%;
}

.MihomeMain>.page-navbar>.page-title>span.add{
  /* border:""; */
  right:10px;
  left:"";
  transition:all 1s linear;/*过渡*/
}

.MihomeMain>.page-navbar>.page-title>span.arrow{
  /* border:"1px solid #666666"!important; */
  right:"";
  left:10px;
  transition:all 1s linear;/*过渡*/
}


.mint-navbar,.mint-cell{
  background:transparent;
}

.my-box{
  background-color:#FFFFFF;
  width:50%;
  border-radius:5px;
  padding:5px 10px;
  box-shadow:0 5px 5px 0px #666666;
  margin:10px 10px;
}

/* 模态框部分 */
.modalBox{
  position: absolute;
  background-color:rgba(0, 0, 0, .5);
  z-index:900;
  height:100%;
  width:100%;
  top:0;
}

.content{
  position: absolute;
  background-color:white;
  /* width:50%; */
  z-index:1000;
  right:5px;
  border-radius:5px;
  top:70px;
}

.content>ul>li{
  padding:5px;
  border-bottom:1px solid rgb(128, 128, 128,.5);
  font-size:16px;
}

.page-part>span.mui-icon{
  color:#FFFFFF;
  line-height: 26px;
  font-size:16px;
  padding:10px;
}

/* 做音乐部分 */
  label{
    display: block;
  }

  label>div{
    border:1px solid rgb(36,55,97,.8);
    display: inline-block;
    padding:10px;
    border-radius:5px;
    color:rgb(36,55,97,.8);
    background-color:rgba(255, 255, 255, 0.3);
  }

  label>div:active{
    color:blue;
  }

  .tabItem2{
    text-align: center;
    margin-top:30px;
  }

  .tabItem2>img{
    width:300px;
    height:300px;
  }

  .tabItem2>h3{
    color:rgb(36,55,97,.8);
    text-shadow:2px 2px 2px white;
  }  

  /* 做视频部分 */
  .tabItem3 video{
    width:100%;
  }

  /* 做开关门的效果 */
  #right{
    background-color:#FFFFFF;
    width:0;
    height:100%;
    overflow: auto;
    display: inline-block;
    position: fixed;
    right:0;
    top:0;
    transition:all .5s linear;/*过渡*/
  }

  #left{
    width:100%;
    overflow:hidden;/*宽变为0时,隐藏*/
    transition:all .5s linear;/*过渡*/
  }


</style>



